  import React, { Component } from 'react';

import {  RenderLineChart, genData } from './Charts';


export class PumpsESys extends Component {
  constructor(props) {
   super(props);

   this.state = {
    //  eng: false,
   }
 }

 componentDidMount() {
    let today = new Date();
    let yesterday = today.setDate(today.getDate() - 1);
    this.props.getESystemsDataDetail({ws: 'pumps', t1: yesterday, t2: today})
 }

 render () {
   const { detailDataList } = this.props
   const data = genData(detailDataList)
   
   return (
    <div className='card card-style'>
      <div className='content'>
        <div className='d-flex justify-content-start'>
          <h4 className='mr-5'>Насосная станция</h4>
          <h5 onClick={() => this.props.setTab({name: 'mainTab'})}>назад</h5>
        </div>

        <RenderLineChart data={data}/>
      </div>
    </div>
   )
 }
}