import _ from 'lodash';
import moment from 'moment';

export const toggleArray = (arr: Array<String>, string: string) => {
    if (arr.indexOf(string) === -1) {
      return [...arr, string]
    } else {
      return arr.filter(item => item !== string)
    }
  }

export const lodashToggle = (array, item) => _.xor(array, [item])

export const toggleArrayLocations = (arr: Array<Object>, location: Object) => {
  let alreadyIn = false;
  let index = -1;
  for (var i = 0; i < arr.length; i++){
    if (_.isEqual(arr[i], location)){
      alreadyIn = true;
      index = i;
      break;
    }
  }

  if (alreadyIn){
    arr.splice(index, 1);
    return arr
  } else {
    return [...arr, location]
  }
}

export const addItemToArray = (arr: Array<String>, string: string) => {
    return [...arr, string]
  }

export const removeItemFromArray = (arr: Array<String>, string: string) => {
    return arr.filter(item => item !== string)
}

export const uniq = a => [...new Set(a)]


export const convertSowsByTours2 = (sowsByToursElemList) => {
  let outputDict = {};
  sowsByToursElemList.map((listElem) => {
    let columns = {};
    outputDict[listElem['tour']['id']] = columns;
    columns['count'] = listElem['count'];
    columns['checked'] = false;
    columns['rows'] = {};
    listElem['sows'].map(sowElem => {
      columns['rows'][sowElem['farm_id']] = {}
      columns['rows'][sowElem['farm_id']]['active'] = false
      columns['rows'][sowElem['farm_id']]['id'] = sowElem['id']
      columns['rows'][sowElem['farm_id']]['status'] = sowElem['status']
    });
  });
  return outputDict
}

export const convertSowsByTours = (sowsByToursElemList) => {
  let outputDict = {};
  sowsByToursElemList.map((listElem) => {
    let columns = {};
    outputDict[listElem['title']] = columns;
    columns['count'] = listElem['count'];
    columns['checked'] = false;
    columns['rows'] = {};
    listElem['sows'].map(sowElem => {
      columns['rows'][sowElem['id']] = {}
      columns['rows'][sowElem['id']]['active'] = false
      columns['rows'][sowElem['id']]['id'] = sowElem['id']
      columns['rows'][sowElem['id']]['status'] = sowElem['status']
      columns['rows'][sowElem['id']]['farm_id'] = sowElem['farm_id']
    });
  });
  return outputDict
}


export const getObjectbyId = (list, id) => {
  let obj = null
  list.map(element => {
    if (element['id'] == id)
      obj = element
  })
  return obj
}

export const getObjectInListbyFieldValue = (list, field, value) => {
  let obj = null
  list.map(element => {
    if (element[field] == value)
      obj = element
  })
  return obj
}

export const toggleArrayDictById= (arr: Array<Object>, obj: Object) => {
  let alreadyIn = false;
  let index = -1;

  for (var i = 0; i < arr.length; i++){
    if (arr[i].id === obj.id){
      alreadyIn = true;
      index = i;
      break;
    }
  }

  if (alreadyIn){
    arr.splice(index, 1);
    return arr
  } else {
    return [...arr, obj]
  }
}

export const getDate = () => {
  let today = new Date();
  let dd = String(today.getDate()).padStart(2, '0');
  let mm = String(today.getMonth() + 1).padStart(2, '0');
  let yyyy = today.getFullYear();

  today = yyyy + '-' + mm + '-' + dd;
  let month_ago = (mm - 1) > 0 ? (mm-1).toString() : (12).toString();
  month_ago = month_ago < 10 ? '0' + month_ago : month_ago
  let month_ago_day = yyyy + '-' + month_ago + '-' + dd;
  return {date_after: month_ago_day, date_before: today}
}

export function getToday() {
  let today = new Date();
  let dd = String(today.getDate()).padStart(2, '0');
  let mm = String(today.getMonth() + 1).padStart(2, '0');
  let yyyy = today.getFullYear();

  today = yyyy + '-' + mm + '-' + dd;
  return today
}

export function dateToDateTime(date) {
  let today = new Date();  
  return date + `T${today.getHours()}:${today.getMinutes()}`
}

export function jsDateTimeToStr (jsDate) {
  let dd = String(jsDate.getDate()).padStart(2, '0');
  let mm = String(jsDate.getMonth() + 1).padStart(2, '0');
  let yyyy = jsDate.getFullYear();
  let hours = jsDate.getHours();
  let minutes = jsDate.getMinutes() > 9 ? jsDate.getMinutes(): '0' + jsDate.getMinutes();
  return yyyy + '-' + mm + '-' + dd + 'T' + hours + ':' + minutes
  
}

export function strDateTimeToTs (strDate) {
  let pDate = strDate.split("T")[0];
  pDate = pDate.split("-")
  let strTime = strDate.split("T")[1];
  strTime = strTime.split(":")
  var newDate = new Date( pDate[0], pDate[1] - 1, pDate[2], strTime[0], strTime[1]);
  return newDate.getTime();
}

export function strDateToTs (strDate) {
  strDate = strDate.split("-")
  var newDate = new Date( strDate[0], strDate[1] - 1, strDate[2]);
  return newDate.getTime();
}

export function jsDateTimeToTs (jsDateTime) {
  return Date.parse(jsDateTime)
}

export function tsToStr (tsDateTime) {
  let date = new Date(tsDateTime);
  let dd = String(date.getDate()).padStart(2, '0');
  let mm = String(date.getMonth() + 1).padStart(2, '0');
  let yyyy = date.getFullYear();
  let hours = date.getHours();
  let minutes = "0" + date.getMinutes();
  // let seconds = "0" + date.getSeconds();
  return yyyy + '-' + mm + '-' + dd + 'T' + hours + ':' + minutes.substr(-2);
}

export function tsToJsDatetime (tsDateTime) {
  return new Date(tsDateTime)
}

export function strToJsDateTime (strDateTime) {
  return new Date(strDateTime)
}

export function tsToStrISO (tsDateTime) {
  let date = new Date(tsDateTime);
  let dd = String(date.getDate()).padStart(2, '0');
  let mm = String(date.getMonth() + 1).padStart(2, '0');
  let yyyy = date.getFullYear();
  let hours = date.getHours();
  let minutes = date.getMinutes();
  if (minutes.length < 2 || minutes === 0) {
    minutes = '0' + minutes
  }
  // 2015-03-15T13:03:00Z
  return yyyy + '-' + mm + '-' + dd + 'T' + hours + ':' + minutes + ':00Z';
}

// moment
export function tsToMoment (tsDateTime) {

  return moment(tsDateTime).toDate()
}