import { LineChart, Line, CartesianGrid, XAxis, YAxis, Tooltip  } from 'recharts';


function genData (data) {
  let dataParams = []
  data.map(record => {
    if (record.record && record.record.insidetemp )
      dataParams = lodashToggle(dataParams, {ts: tsToStr(record.ts), темп: record.record.insidetemp})
  })

  return dataParams
}

function RenderLineChart (props) {
  const { data } = props
  return (
    <LineChart width={600} height={300} data={data}>
      <Line type="monotone" dataKey="темп" stroke="#BF263C" />
      <CartesianGrid stroke="#ccc" />
      <XAxis dataKey="ts" />
      <YAxis />
      <Tooltip />
    </LineChart>
  )
}

